<template>

<div>
<h1 class="title"></h1>
    <div class="columns is-multiline is-mobile">

    <div v-for="course in courses" class="column is-two-thirds-mobile is-half-tablet is-half-desktop">
       <course-card :course="course"></course-card>
    </div>
    
    </div>

</div>




</template>

</template>

<script>

import FullCard from '../components/course/FullCard.vue'

    export default {
        name:'CourseIndex',
        components:{
            'course-card':FullCard
        },

        data(){
            return{
                courses:[]
            }
        },
        methods:{
            fetchData(){
                let center={ name:'' }
                let teacher={ name:'' , parent:0 }
                let course={
                    id: 1,
                    name:'',
                    status:'',
                    dayofweek:'',
                    cost: 3000,
                    time:'18:00-20:00',
                    hours: 30,
                    period:'2017/2/3-2017/5/22',
                    weeks: 15,
                    description:'',
                    center:center,
                    teacher:teacher
                }
                let courseList=[
                    course,
                    course,
                    course,
                    course,
                ]
                this.courses=courseList

            }
        },
        beforeMount(){
            this.fetchData()
        }
    }
</script>


