<template>
<div>
    <large-card :course="course"></large-card>
     <h1 class="title"></h1>
     <info></info>

     <h1 class="title"></h1>
     <teacher></teacher>

     <h1 class="title"></h1>
      <table class="table" style="font-size:17px;">
          <tbody>
              <tr v-for="schedule in course.schedule">
                <td class="order-td">
                  {{schedule.lesson}}
                </td>
                <td>
                  {{schedule.content}}
                </td>
              </tr>              
            </tbody>
      </table>

      <h1 class="title"></h1>
      <li v-for="notice in course.notices" v-text="notice" style="font-size:17px;"></li>
     <!--  <li></li>
      <li></li> -->
</div>
</template>

<script>
import LargeCard from  '../../components/course/LargeCard.vue'
import Info from  '../../components/course/Info.vue'
import Teacher from  '../../components/course/Teacher.vue'

export default {

   name:'details',
   components:{
      'large-card':LargeCard,
      'info':Info,
      'teacher':Teacher
   },
   data(){
     return {
        course:{}
     }
   },
   beforeMount(){
       this.fetchData()
   },
   methods:{
        fetchData(){
          let schedule=[
            { lesson:'1' , content:''},
            { lesson:'2' , content: ''},
            { lesson:'3' , content:''},
            { lesson:'4' , content:''},
            { lesson:'5' , content:''},
          ]
          let notices=[
              '',
               ''
          ]
            let course={
                name:'',
                status:'',
                dayofweek:'',
                cost: 3000,
                time:'10:00 ~ 12:00',
                hours: 32,
                startDate:'2017-3-16',
                period:'2017-3-16 ~ 2017-6-30',
                weeks: 16,
                description:'',
                teacher:{ name:'' ,parent:0},
                center:{name:''},
                schedule:schedule,
                notices:notices
            }
            this.course=course
        }
   },


}
</script>


<style>
.course-info {
    font-size:17px;
}
.order-td{
    width: 10%;
    text-align: center;
}
    
</style>




